.app-header {
    height: 60px;
    padding-inline: 35px;
    padding-block: 10px;
    background-color: $clr001;
    color: $clr007;
    font-size: 18px;
    @include for-mobile-layout {
            padding-inline: 10px;
            font-size: 14px;
    }
    .logo {
        gap: 10px;
        font-size: 18px;
        font-weight: 500;
        img {
            height: 45px;
        }
        @include for-mobile-layout {
            img {
                display: none;
            }
        }
    }
    nav {
        gap: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        @include for-mobile-layout {
           gap: 12px;
        }
        a {
            padding-block: 16px;
        }
        .active {
            scale: 1.08;
            border-bottom: 1px solid $clr007;
        }
    }
}