.move-list {
    text-transform: none;
    font-size: 16px;
    font-family:sans-serif;
    padding-left: 5px;
    margin-top: 5px;
    width: 60%;
    padding: 15px;
    ul {
        list-style: none;
        padding: 0;
        margin-top: 5px;
        height: 115px;
        overflow-y: scroll;
        @include for-mobile-layout {
            height: 60px;

        }
        li {
            display: flex;
            align-items: center;
            justify-content: space-around;
            background-color: rgb(222, 222, 220);
            padding: 5px;
            border-radius: 4px;
            margin-top: 5px;
            box-shadow: 0 0 5px #0000002b;

            .amount {
                width: 40px;
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 3px;
                svg {
                    margin-top: 2px;
                }
            }
        }
    }
    @include for-mobile-layout {
        position: absolute;
        bottom: 20px;
        width: 100%;
        font-size: 14px;
    }
}